import { Component, Prop, Event, EventEmitter, h, Host } from '@stencil/core';

export interface BreadcrumbItem {
  label: string;
  path?: string;
  isActive?: boolean;
}

@Component({
  tag: 'bh-appbar',
  styleUrl: 'bh-appbar.css',
  shadow: true,
})
export class BhAppbar {
  /**
   * Whether the menu is open
   */
  @Prop() isMenuOpen: boolean = false;

  /**
   * Logo source URL
   */
  @Prop() logoSrc?: string;

  /**
   * Logo alt text
   */
  @Prop() logoAlt: string = 'Solstice';

  /**
   * Breadcrumb items
   */
  @Prop() breadcrumbItems: BreadcrumbItem[] = [
    { label: 'Dashboard', path: '/dashboard' },
    { label: 'Dashboards', isActive: true },
  ];

  /**
   * Notification count
   */
  @Prop() notificationCount: number = 0;

  /**
   * Event emitted when menu toggle is clicked
   */
  @Event() bhMenuToggle!: EventEmitter<void>;

  /**
   * Event emitted when a breadcrumb is clicked
   */
  @Event() bhBreadcrumbClick!: EventEmitter<BreadcrumbItem>;

  /**
   * Event emitted when notification icon is clicked
   */
  @Event() bhNotificationClick!: EventEmitter<void>;

  /**
   * Event emitted when calendar icon is clicked
   */
  @Event() bhCalendarClick!: EventEmitter<void>;

  /**
   * Event emitted when settings icon is clicked
   */
  @Event() bhSettingsClick!: EventEmitter<void>;

  private handleMenuToggle = () => {
    this.bhMenuToggle.emit();
  };

  private handleBreadcrumbClick = (item: BreadcrumbItem) => {
    if (item.path && !item.isActive) {
      this.bhBreadcrumbClick.emit(item);
    }
  };

  private handleNotificationClick = () => {
    this.bhNotificationClick.emit();
  };

  private handleCalendarClick = () => {
    this.bhCalendarClick.emit();
  };

  private handleSettingsClick = () => {
    this.bhSettingsClick.emit();
  };

  render() {
    return (
      <Host>
        <header class="appbar">
          {/* Left Section */}
          <div class="left-section">
            {/* Menu Toggle */}
            <bh-button-icon
              hierarchy="quaternary"
              size="sm"
              iconName={this.isMenuOpen ? 'menu_open' : 'last_page'}
              onClick={this.handleMenuToggle}
            ></bh-button-icon>

            {/* Logo */}
            {this.logoSrc && (
              <div class="logo">
                <img src={this.logoSrc} alt={this.logoAlt} class="logo-image" />
              </div>
            )}

            {/* Property Switcher Slot */}
            <slot name="property-switcher"></slot>

            {/* Breadcrumbs */}
            <bh-breadcrumbs
              items={this.breadcrumbItems}
              showHome={true}
              onBhItemClick={(e) => this.handleBreadcrumbClick(e.detail)}
            ></bh-breadcrumbs>
          </div>

          {/* Right Section */}
          <div class="right-section">
            {/* Calendar Icon */}
            <div class="icon-button-wrapper">
              <bh-button-icon
                hierarchy="quaternary"
                size="sm"
                iconName="calendar_today"
                onClick={this.handleCalendarClick}
              ></bh-button-icon>
              <bh-badge-dot
                size="sm"
                color="success"
                outline={true}
                class="icon-badge-dot"
              ></bh-badge-dot>
            </div>

            {/* Settings Icon */}
            <div class="icon-button-wrapper">
              <bh-button-icon
                hierarchy="quaternary"
                size="sm"
                iconName="settings"
                onClick={this.handleSettingsClick}
              ></bh-button-icon>
              <bh-badge-dot
                size="sm"
                color="blue"
                outline={true}
                class="icon-badge-dot"
              ></bh-badge-dot>
            </div>

            {/* Notifications */}
            <div class="icon-button-wrapper">
              <bh-button-icon
                hierarchy="quaternary"
                size="sm"
                iconName="notifications"
                onClick={this.handleNotificationClick}
              ></bh-button-icon>
              {this.notificationCount > 0 && (
                <bh-badge-dot
                  size="sm"
                  color="error"
                  outline={true}
                  class="icon-badge-dot"
                ></bh-badge-dot>
              )}
            </div>
          </div>
        </header>
      </Host>
    );
  }
}
